var self;
export default{
		
	data(){
		return{
			id:'',
			data:Object,
			baseUrl:"http://39.105.48.243:8080/crlink/"
		}
	},
	onLoad(e) {
		self = this
		self.id = e.id
		self.getData()
	},
	methods:{
		getData(){
			let loginkey = uni.getStorageSync('loginKey')
			this.$api.post('user!ajaxGetCertDetail.action', {
				loginKey: loginkey,
				id:self.id
			
			}).then(res => {
				if(res.res.status==0)
				self.data = res.inf
				})
		}
	}
}
